import { Home } from "./Home";
import { TodoList } from "./TodoList";
import { Link } from "react-router-dom";
import { routes } from "../config/routes";
export const NavigationBar = () => {
    return (
        <div className= "Menu">
            {routes.map((item) => (
                <Link to={item.path} className="Menu-Item">{item.label}</Link>
            ))}
        </div>

    );
}