import { Home } from "../component/Home";
import { TodoList } from "../component/TodoList";
import { NotFound } from "../component/NotFound";
import { Timeline} from "../component/TimeLine";
import TodoForm from "../component/TodoForm";
export const routes = [
    {
        path: '/home',
        label: 'HOME',
        component: Home
    },
    {
        path: '/todolist',
        label: 'TODO List',
        component: TodoList
    },
    {
        path: '/todo form',
        label: 'Add New Todo',
        component: TodoForm
    },
    {
        path: '/timeline',
        label: 'Time line',
        component: Timeline
    },
]