import React, { useEffect } from "react";
import { Row, Col, Form, Input, Button, DatePicker } from "antd";
import { useDispatch } from "react-redux";
import { v4 as uuidv4 } from "uuid";
import moment from "moment";
import { addTodo } from "../actions/action";

function TodoForm(props) {
  const dispatch = useDispatch();
  const onFinish = (values) => {
    const data = {
      id: uuidv4(),
      name: values.name,
      time: moment(values.time).format("DD/MM/YYYY").toString(),
      complete: false,
    };

    dispatch(addTodo(data));
    // toast.success("Thành công", "Thêm công việc thành công");
  };

  return (
    <div className='container'>
      <div className = "todo_form">
      <Row>
        <h2 className="title-Form">New todo</h2>
      </Row>
      {/* <Row>
        <Col span={12}> */}
          <Form
            name="basic"
            // labelCol={{
            //   span: 8,
            // }}
            // wrapperCol={{
            //   span: 16,
            // }}
            initialValues={{}}
            onFinish={onFinish}
            autoComplete="off"
          >
            <Form.Item noStyle
              name="name"
              rules={[
                {
                  required: true,
                  message: "Vui lòng nhập tên công viêc!",
                },
          ]}
          className="form_Item"
            >
              <Input classnam="form_Item" placeholder="Tên công việc" />
            </Form.Item>

            <Form.Item noStyle
              name="time"
              rules={[
                {
                  required: true,
                  message: "Vui lòng chọn thời gian !",
                },
              ]}
              className="form_Item"
            >
              <DatePicker  placeholder="Thời gian hết hạn" format="DD/MM/YYYY" />
            </Form.Item>

            <Form.Item noStyle
              // wrapperCol={{
              //   offset: 8,
              //   span: 16,
              // }}
            >
              <Button type="primary" htmlType="submit">
                Thêm công việc
              </Button>
            </Form.Item>
          </Form>
        {/* </Col>
      </Row> */}
    </div>
    </div>
  );
}

export default TodoForm;